<template>
  <div 
  @click="openDrawerss(index)"
  :class='"theme theme"+index'
        :style="mainStyle"  >
            <div :style='(hover)? enter : leave'
            @mouseenter="hover=!hover" 
            @mouseleave="hover=!hover"
          >theme {{index}}
          </div>
          </div>
</template>

<script>
import $ from 'jquery'
import f from '../../JsLib/drawer-bootstrap'
export default {
// name:"ThemesComponent",

data(){
    return({
        hover:false,
    })
},
props:{
    mainStyle:String,
    index:Number,
    leave:String,
    enter:String
}
,methods:{
        openDrawerss(index){
            f.drawer({
      exitResizeing: false,
      withNavItemBootstrap: false,
      withNavbarBrandBootstrap: false,
      withDropDownBotstrap: false,
      customJqueryElement: $(".nav .nav-item").clone(),
      customHeadDrawer: '<div></div>',
      addQuery:function () {
        //   $(".drawer a").attr('href',"/api");
          $(".drawer a").each(function () {
              let ht=$(this).html()
              ht=ht.replace(/ +/g,'-')
              $(this).attr('href',"/"+ht);
          })
      },
      headerStyle: {},
      class: {
        showButton: "openDrawer"
      },
      defualtStylesPosition: index
    })
    f.drawerShow()
        }
}
}
</script>

